'use client'

import { useEffect, useState } from 'react'
import { Card, CardContent } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { Progress } from '@/components/ui/progress'
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select'
import { Edit, Eye } from 'lucide-react'

type Farmer = {
  id: number
  first_name: string
  last_name: string
}

type Crop = {
  id: number
  crop_name: string
  field_name: string
  location: string
  variety: string
  area: number
  stage: string
  stage_progress: number
  sowing_date: string
  expected_harvest: string
  health: string
  expected_yield: string
}

export function CropsList() {
  const [farmers, setFarmers] = useState<Farmer[]>([])
  const [selectedFarmer, setSelectedFarmer] = useState<string>('all')
  const [crops, setCrops] = useState<Crop[]>([])
  const [loading, setLoading] = useState(false)

  // ✅ FETCH FARMERS
  useEffect(() => {
    const fetchFarmers = async () => {
      const token = localStorage.getItem('authToken')
      if (!token) return

      const res = await fetch(
        'https://rda.ngrok.app/api/subadmin/assigned-farmers/',
        {
          headers: {
            Authorization: `Bearer ${token}`,
            'ngrok-skip-browser-warning': 'true',
          },
        }
      )

      const data = await res.json()
      setFarmers(data.assigned_farmers || [])
    }

    fetchFarmers()
  }, [])

  // ✅ FETCH ALL CROPS (DEFAULT)
  const fetchAllCrops = async () => {
    setLoading(true)
    const token = localStorage.getItem('authToken')

    const res = await fetch(
      'https://rda.ngrok.app/api/crops/',
      {
        headers: {
          Authorization: `Bearer ${token}`,
          'ngrok-skip-browser-warning': 'true',
        },
      }
    )

    const data = await res.json()
    setCrops(data.data || [])
    setLoading(false)
  }

  // ✅ FETCH CROPS BY FARMER
  const fetchCropsByFarmer = async (farmerId: string) => {
    setLoading(true)
    const token = localStorage.getItem('authToken')

    const res = await fetch(
      `https://rda.ngrok.app/api/crops/farmer/${farmerId}/`,
      {
        headers: {
          Authorization: `Bearer ${token}`,
          'ngrok-skip-browser-warning': 'true',
        },
      }
    )

    const data = await res.json()
    setCrops(data.data || [])
    setLoading(false)
  }

  // ✅ LOAD ALL CROPS ON PAGE LOAD
  useEffect(() => {
    fetchAllCrops()
  }, [])

  const stageColors: Record<string, string> = {
    'Growth & Tillering': 'bg-primary/20 text-primary',
    'Pre-Sowing': 'bg-secondary/20 text-secondary',
    'Maturity & Harvesting': 'bg-accent/20 text-accent',
  }

  return (
    <div className="space-y-6">
      {/* ✅ FARMER DROPDOWN */}
      <Card>
        <CardContent className="pt-6">
          <Select
            value={selectedFarmer}
            onValueChange={(value) => {
              setSelectedFarmer(value)
              value === 'all'
                ? fetchAllCrops()
                : fetchCropsByFarmer(value)
            }}
          >
            <SelectTrigger>
              <SelectValue placeholder="Select Farmer" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Farmers</SelectItem>
              {farmers.map((farmer) => (
                <SelectItem
                  key={farmer.id}
                  value={farmer.id.toString()}
                >
                  {farmer.first_name} {farmer.last_name}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </CardContent>
      </Card>

      {/* ✅ CROPS LIST */}
      {loading && <p className="text-muted-foreground">Loading crops…</p>}

      {!loading &&
        crops.map((crop) => (
          <Card key={crop.id} className="hover:shadow-lg">
            <CardContent className="pt-6">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="space-y-4">
                  <div>
                    <div className="flex justify-between mb-2">
                      <h3 className="text-xl font-bold">
                        {crop.crop_name}
                      </h3>
                      <Badge
                        className={
                          stageColors[crop.stage] ||
                          'bg-muted text-foreground'
                        }
                      >
                        {crop.stage}
                      </Badge>
                    </div>
                    <p className="text-sm text-muted-foreground">
                      {crop.field_name}
                    </p>
                    <p className="text-xs text-muted-foreground">
                      {crop.location}
                    </p>
                  </div>

                  <div className="grid grid-cols-2 gap-3">
                    <div className="bg-muted/50 rounded p-2">
                      <p className="text-xs text-muted-foreground">
                        Variety
                      </p>
                      <p className="font-semibold text-sm">
                        {crop.variety}
                      </p>
                    </div>
                    <div className="bg-muted/50 rounded p-2">
                      <p className="text-xs text-muted-foreground">
                        Area
                      </p>
                      <p className="font-semibold text-sm">
                        {crop.area} acres
                      </p>
                    </div>
                  </div>

                  <div>
                    <div className="flex justify-between mb-2">
                      <p className="text-xs font-semibold text-muted-foreground">
                        Stage Progress
                      </p>
                      <span className="text-xs font-semibold">
                        {crop.stage_progress}%
                      </span>
                    </div>
                    <Progress
                      value={crop.stage_progress}
                      className="h-2"
                    />
                  </div>
                </div>

                <div className="space-y-4">
                  <div className="grid grid-cols-2 gap-3">
                    <div className="bg-muted/50 rounded p-2">
                      <p className="text-xs text-muted-foreground">
                        Sowing Date
                      </p>
                      <p className="font-semibold text-sm">
                        {crop.sowing_date}
                      </p>
                    </div>
                    <div className="bg-muted/50 rounded p-2">
                      <p className="text-xs text-muted-foreground">
                        Expected Harvest
                      </p>
                      <p className="font-semibold text-sm">
                        {crop.expected_harvest}
                      </p>
                    </div>
                  </div>

                  <div className="grid grid-cols-2 gap-3">
                    <div className="bg-primary/10 rounded p-2">
                      <p className="text-xs text-muted-foreground">
                        Health
                      </p>
                      <p className="font-semibold text-sm text-primary">
                        {crop.health}
                      </p>
                    </div>
                    <div className="bg-secondary/10 rounded p-2">
                      <p className="text-xs text-muted-foreground">
                        Expected Yield
                      </p>
                      <p className="font-semibold text-sm text-secondary">
                        {crop.expected_yield}
                      </p>
                    </div>
                  </div>

                  <div className="flex gap-2 pt-2">
                    <Button size="sm" variant="outline" className="flex-1">
                      <Eye size={16} className="mr-1" />
                      View
                    </Button>
                    <Button size="sm" variant="outline" className="flex-1">
                      <Edit size={16} className="mr-1" />
                      Edit
                    </Button>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
        ))}

      {!loading && crops.length === 0 && (
        <p className="text-muted-foreground">
          No crops found.
        </p>
      )}
    </div>
  )
}
